.tb_rt_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: auto;
}

.tb_rt_post_slider{
    width: 100%;
    overflow: hidden;
}

.tb_rt_post_wrapper{
    width: 100%;
    padding: 30px 10px;
    overflow: hidden;
}

.tb_rt_fixed_height .tb_rt_post_wrapper, 
.tb_rt_fixed_height .tb_rt_post_in{
    height: 100%;
}

.tb_rt_post_in {
    display: flex;
    flex-direction: column;
    position: relative;
    border-radius: var(--tb-border-radius);
    overflow: hidden;
    cursor: pointer;
    transition: box-shadow .3s ease-in, transform .3s ease-in;
}

.tb_rt_post_in_animate:hover{
    transform: scale(1.04);
    transition: box-shadow .3s ease-in, transform .3s ease-in;
}
.tb_rt_media_wrap{
    position: relative;
}
.tb_rt_image{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    transition: transform 3.525s .01s ease-out, opacity 3.525s ease-out;
}

.tb_rt_social_ico_ {
    font-size: 22px;
    margin: 15px 0;
    color: var(--tb-icon-color);
}

.tb_rt_play_ico {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 70px;
    color: #fff;
    opacity: 0;
    transition-property: opacity;
    transition-timing-function: ease-in;
    transition-duration: .3s;
    transition-delay: .5s;
}

.tb_rt_post_in:hover .tb_rt_play_ico{
    opacity: .5;
    transition-property: opacity;
    transition-timing-function: ease-in;
    transition-duration: .3s;
}

.tb_rt_image_wrap_, .tb_rt_video_wrap_{
    padding-bottom: 178%;
    position: relative;
    background-color: rgba(0,0,0,.03);
    pointer-events: none;
    user-select: none;
    background-size: cover;
    background-position: center;
}
.tb_rt_image, .tb_rt_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
    background-color: #000;
}

.tb_rt_video video{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
}

.tb_rt_media_wrap .fb_iframe_widget_fluid{
    width: 100%;
    display: flex;
    align-items: center;
}

.tb_rt_video video::-webkit-media-controls-panel {
    background-image: linear-gradient(transparent, transparent) !important;
}

.tb_rt_post_in:hover .tb__media_ico_ .tb__icon,
.tb_rt_post_in:hover .tb__media_ico_ .tb__icon{
    opacity: 0;
    transform: scaleX(.4) scaleY(.4) translateZ(0);
}

.tb_rt_post_details, .tb_rt_post_overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px;
    opacity: 0;
    z-index: 1;
    visibility: hidden;
    width: calc(100% + 1px);
    height: calc(100% + 1px);
}

.tb_rt_post_details {
    z-index: 4;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

}

.tb_rt_post_overlay-light{
    background-color: rgba(255, 255, 255, .3);
    background-image: linear-gradient(135deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, .4) 100%);
}

.tb_rt_post_overlay-dark{
    background-color: rgba(0, 0, 0, .2);
    background-image: linear-gradient(135deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .4) 100%);
}

@media only screen and (min-width: 991px)  {
    .tb_rt_post_in:hover .tb_rt_post_details,
    .tb_rt_post_in:hover .tb_rt_post_overlay {
        opacity: 1;
        visibility: visible;
    }

    .tb_rt_post_in:hover .tb_rt_image {
        transform: scaleX(1.1) scaleY(1.1) translateZ(0);
    }
    .tb_rt_post_animate:hover .tb_rt_image {
        -webkit-transform: scale(1.07);
        transform: scale(1.07);
    }

    .tb_rt_post_in:hover .tb_rt_post_details{
        transform: translateY(10px);
        animation: TbSlideUpAnimation_ .65s ease forwards;
    }

    @-webkit-keyframes TbSlideUpAnimation_ {
        100% {
            transform: translateY(0);
        }
        100% {
            transform: translateY(0);
        }
    }

    @keyframes TbSlideUpAnimation_ {
        0% {
            transform: translateY(10px);
        }
        100% {
            transform: translateY(0);
        }
    }
}
